import React from "react";
import { FormControl, FormLabel, Badge, Input } from "@chakra-ui/react";

const FormInput = ({
  id,
  isRequired,
  title,
  value,
  handler,
  type = "text",
}) => {
  return (
    <FormControl id={id} isRequired={isRequired} margin={"8px 0px 0px 0px"}>
      <FormLabel>
        {" "}
        <Badge
          padding={1}
          paddingLeft={2}
          paddingRight={2}
          borderRadius="md"
          colorScheme={"purple"}
          color="black"
        >
          {title}
        </Badge>
      </FormLabel>
      <Input
        type={type}
        value={value}
        onChange={(e) => {
          handler(e.target.value);
        }}
      />
    </FormControl>
  );
};

export default FormInput;
