import {
  Badge,
  Box,
  Container,
  Radio,
  RadioGroup,
  Stack,
  Text,
  Textarea,
} from "@chakra-ui/react";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { UserState } from "../Context/UserProvider";
import FormInput from "./FormInput";
import FormButton from "./FormButton";
import ErrorComponent from "./ErrorComponent";
import NoteGrid from "./NoteGrid";

const NotePageBody = () => {
  const { user, notes, setNotes } = UserState();
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [notepassword, setNotepassword] = useState("");
  const [confirmnotepassword, setConfirmNotePassword] = useState("");
  const [radiovalue, setradioValue] = useState("2");
  const [errorMessage, setErrorMessage] = useState("");
  const [errorColor, setErrorColor] = useState("red");

  const fetchNotes = async () => {
    try {
      const config = {
        headers: {
          Authorization: user.token,
        },
      };
      const { data } = await axios.get(
        "http://192.168.56.16:3000/api/note/fetchallnotes",
        config
      );
      console.log(data);
      if (data.sucess === true) setNotes(data.message);
    } catch (error) {
      console.log(error);
    }
  };

  //note creation both public and private
  const createNoteHandler = async (e) => {
    try {
      //public note api call

      if (radiovalue === "2") {
        console.log("public note creation");
        if (!title || !content) {
          setErrorMessage("ALL FILEDS REQUIRED");
          setTimeout(() => {
            setErrorMessage("");
          }, 2000);
          return;
        }
        const config = {
          headers: {
            "Content-Type": "application/json",
            Authorization: user.token,
          },
        };
        const { data } = await axios.post(
          "http://192.168.56.16:3000/api/note/createnote",
          {
            title: title,
            content: content,
          },
          config
        );
        if (data.sucess === true) {
          console.log("public note", data.message);
          setNotes((notes) => {
            return [...notes, data.message];
          });
        }
        console.log({ data });
      } else if (radiovalue === "1") {
        //private note api call
        console.log("private note creation");
        if (!title || !content || !notepassword || !confirmnotepassword) {
          setErrorMessage("ALL FILEDS REQUIRED");
          setTimeout(() => {
            setErrorMessage("");
          }, 2000);
          return;
        }
        if (notepassword !== confirmnotepassword) {
          setErrorMessage("NOTES PASSWORD DOESNOT MATCH");
          setTimeout(() => {
            setErrorMessage("");
          }, 2000);
          return;
        }
        const config = {
          headers: {
            "Content-Type": "application/json",
            Authorization: user.token,
          },
        };
        const { data } = await axios.post(
          "http://192.168.56.16:3000/api/note/createprivatenote",
          {
            title: title,
            content: content,
            key: notepassword,
          },
          config
        );
        if (data.sucess === true) {
          setNotes((notes) => {
            return [...notes, data.message];
          });
          setErrorMessage("SUCESSFULLY CREATED");
          setErrorColor("green");
          setTimeout(() => {
            setErrorMessage("");
            setErrorColor("red");
          }, 2000);
        }
      }
    } catch (error) {
      setErrorMessage(error.message);
      setTimeout(() => {
        setErrorMessage("");
      }, 2000);
      console.log(error.message);
    }
  };
  //to fetch all the notes
  useEffect(() => {
    if (!user) return;
    fetchNotes();
    return () => {};
  }, [user]);
  return (
    <div style={{ display: "flex" }}>
      <Container
        p={2}
        bg={"white"}
        marginTop={3}
        maxW={"100%"}
        borderRadius={"lg"}
        flex={1}
      >
        <Box display={"flex"} flexDir={"row"}>
          {/* to add note */}
          <Box flexBasis={"40%"}>
            {errorMessage && (
              <ErrorComponent
                errorMessage={errorMessage}
                errorColor={errorColor}
              />
            )}
            <FormInput
              id={"notetitle"}
              isRequired={true}
              title={"Note Title"}
              value={title}
              handler={setTitle}
            />
            <Box display={"flex"} alignItems={"center"}>
              <Badge
                mt={2}
                padding={1}
                paddingLeft={2}
                paddingRight={2}
                borderRadius="md"
                colorScheme={"purple"}
                color="black"
              >
                {"Enter Your Text Here"}
              </Badge>
              <RadioGroup
                defaultValue="2"
                mt={2}
                ml={"2"}
                value={radiovalue}
                onChange={setradioValue}
              >
                <Stack spacing={5} direction="row">
                  <Radio colorScheme="red" value="1">
                    Private 🔏
                  </Radio>
                  <Radio colorScheme="green" value="2">
                    Public 🔓
                  </Radio>
                </Stack>
              </RadioGroup>
            </Box>
            <Textarea
              placeholder="Here is a sample placeholder"
              rows={7}
              marginTop={2}
              value={content}
              onChange={(e) => {
                setContent(e.target.value);
              }}
            />
            {radiovalue === "1" && (
              <FormInput
                id={"notepassword"}
                isRequired={true}
                title={"Note password"}
                value={notepassword}
                type={"password"}
                handler={setNotepassword}
              />
            )}
            {radiovalue === "1" && (
              <FormInput
                id={"ConfirmNotePassword"}
                isRequired={true}
                title={"Confirm Password"}
                value={confirmnotepassword}
                type={"password"}
                handler={setConfirmNotePassword}
              />
            )}
            <FormButton title={"Create Note"} handler={createNoteHandler} />
          </Box>

          {/*editor */}
          <Box flexBasis={"60%"} p={2}>
            <Box
              width={"100%"}
              height={"600px"}
              overflowY={"auto"}
              bg={"yellow.100"}
              p={2}
            >
              <NoteGrid notes={notes} />
            </Box>
          </Box>
        </Box>
      </Container>
    </div>
  );
};

export default NotePageBody;
