import React, { useState } from "react";

import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
  Box,
  Text,
} from "@chakra-ui/react";
import { ViewIcon } from "@chakra-ui/icons";
import BadgeItem from "./BadgeItem";
import FormInput from "./FormInput";
import { UserState } from "../Context/UserProvider";
import ErrorComponent from "./ErrorComponent";

function NonPrivateNote({ note }) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <>
      <ViewIcon onClick={onOpen} mt={2} />
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            <BadgeItem title={"" + note.title + "🔥"} />
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>{note.content}</ModalBody>

          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={onClose}>
              Close
            </Button>
            <Button variant="ghost">Secondary Action</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}

function PrivateNote({ note }) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [key, setKey] = useState("");
  const { selectedNote, noteLoginState, setNoteLoginState } = UserState();
  const [errorMessage, setErrorMessage] = useState("");

  //note login handler
  const noteloginHandler = (e) => {
    if (!key) {
      // dosomething;
      updateErrorMessage("Please Enter the Note Key");
      return;
    }
    if (key !== selectedNote.key) {
      // dosomething
      updateErrorMessage("not key doesnt match");
      return;
    }
    if (key === selectedNote.key) {
      setKey("");
      setNoteLoginState(true);
    }
  };

  //dynamically removing error update
  const updateErrorMessage = (message, timer = 1500) => {
    setErrorMessage(message);
    setTimeout(() => {
      setErrorMessage("");
    }, timer);
  };
  return (
    <>
      <ViewIcon onClick={onOpen} mt={2} />
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            {note?.isPrivate && noteLoginState === false ? (
              <BadgeItem title={"This Note is Private🔥"} />
            ) : (
              <BadgeItem title={note.title} />
            )}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            {note?.isPrivate && noteLoginState === false ? (
              <>
                <FormInput
                  handler={setKey}
                  value={key}
                  type={"password"}
                  id={"noteloginpasswordId"}
                  isRequired={true}
                  title={"Note Key:"}
                />
                {/* will handle node authentication error */}
                <ErrorComponent errorColor="red" errorMessage={errorMessage} />
              </>
            ) : (
              <>{note.content}</>
            )}
          </ModalBody>

          <ModalFooter>
            {/* <Button colorScheme="blue" mr={3} onClick={onClose}>
              Submit
            </Button> */}
            <Button colorScheme="blue" mr={3} onClick={noteloginHandler}>
              Submit
            </Button>
            {/* <Button variant="ghost">Secondary Action</Button> */}
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}

const NoteContentModal = ({ note }) => {
  return (
    <>
      {note?.isPrivate ? (
        <PrivateNote note={note} />
      ) : (
        <NonPrivateNote note={note} />
      )}
    </>
  );
};

export default NoteContentModal;
