import React from "react";
import { Box, Grid, GridItem, Text } from "@chakra-ui/react";
import { Card, CardHeader, CardBody, CardFooter } from "@chakra-ui/react";
import { AiFillEdit } from "react-icons/ai";
import { AiFillDelete } from "react-icons/ai";
import { AiFillUnlock, AiFillLock } from "react-icons/ai";
import { UserState } from "../Context/UserProvider";
import BadgeItem from "./BadgeItem";
import NoteContentModal from "./NoteContentModal";
import axios from "axios";
import NoteEditModal from "./NoteEditModal";

const NoteGrid = ({ notes }) => {
  const {
    user,
    setNotes,
    setSelectedNote,
    selectedNote,
    noteLoginState,
    setNoteLoginState,
  } = UserState();
  //handles edition of the note
  const editHandler = (id) => {
    console.log(id);
  };
  //handles deletion of the note
  const deleteHandler = async (id) => {
    console.log(id);
    try {
      const config = {
        headers: {
          Authorization: user.token,
        },
      };
      const { data } = await axios.delete(
        `http://192.168.56.16:3000/api/note/deletenote/${id}`,
        config
      );
      if (data.sucess == true) {
        setNotes(notes.filter((note) => note._id != id));
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Grid
      templateColumns={{
        base: "repeat(1, 1fr)",
        md: "repeat(2, 1fr)",
        lg: "repeat(3, 1fr)",
      }}
      gap={6}
    >
      {notes &&
        notes.map((note, i) => {
          return (
            <GridItem
              w="100%"
              key={note._id}
              onClick={(e) => {
                setSelectedNote(note);
              }}
            >
              <Card variant={"elevated"}>
                <CardBody>
                  <Box display={"flex"} flexDir={"column"}>
                    <Box height={"60px"} overflowY={"auto"}>
                      <Box>
                        Title : <BadgeItem title={note.title + "🔥"} />
                      </Box>
                    </Box>
                    <Box display={"flex"} justifyContent={"space-between"}>
                      {/* <Text m={1}>
                        <AiFillEdit
                          fontSize={"23px"}
                          onClick={(e) => {
                            editHandler(note._id);
                          }}
                        />
                      </Text> */}
                      <NoteEditModal note={note} />
                      <Text m={1}>
                        <AiFillDelete
                          fontSize={"23px"}
                          onClick={(e) => {
                            deleteHandler(note._id);
                          }}
                        />
                      </Text>
                      {note.isPrivate ? (
                        <Text m={1}>
                          {note.isPrivate &&
                          selectedNote?._id === note._id &&
                          noteLoginState ? (
                            <AiFillUnlock
                              fontSize={"21px"}
                              onClick={(e) => {
                                setSelectedNote("");
                                setNoteLoginState(false);
                              }}
                            />
                          ) : (
                            <AiFillLock fontSize={"21px"} />
                          )}
                        </Text>
                      ) : (
                        <Text m={1}>
                          <AiFillUnlock fontSize={"21px"} />
                        </Text>
                      )}
                      {/* Note details modal */}
                      <NoteContentModal note={note} />
                    </Box>
                  </Box>
                </CardBody>
              </Card>
            </GridItem>
          );
        })}
    </Grid>
  );
};

export default NoteGrid;
