import React, { useState } from "react";
import { AiFillEdit } from "react-icons/ai";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  useDisclosure,
} from "@chakra-ui/react";
import FormInput from "./FormInput";
import { UserState } from "../Context/UserProvider";
import axios from "axios";

function EditNote({ note }) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [newtitle, setNewTitle] = useState(note.title);
  const [newContent, setNewContent] = useState(note.content);
  const { user, notes, setNotes } = UserState();
  const updateNote = async (e) => {
    console.log(newtitle, newContent);
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
          authorization: user.token,
        },
      };
      const { data } = await axios.put(
        `http://192.168.56.16:3000/api/note/updatenote/${note._id}`,
        {
          title: newtitle,
          content: newContent,
        },
        config
      );
      console.log("update", { data });
      if (data.sucess === true) {
        let newNotes = notes.filter((elem) => {
          return elem._id != note._id;
        });
        newNotes = [...newNotes, data.message];
        setNotes(newNotes);
      }
    } catch (error) {
      console.log(error.message);
    }
  };
  return (
    <>
      <AiFillEdit
        style={{ marginTop: "4px", fontSize: "23px" }}
        onClick={onOpen}
      />
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>{"Note To Be Updated:" + note.title}</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <FormInput
              handler={setNewTitle}
              id={"newTitleIdSetUp"}
              isRequired={true}
              title={"New Title "}
              value={newtitle}
            />
            <FormInput
              handler={setNewContent}
              id={"newContentIdSetUp"}
              isRequired={true}
              title={"New Content "}
              value={newContent}
            />
          </ModalBody>

          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={updateNote}>
              Update
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}
const NoteEditModal = ({ note }) => {
  return <EditNote note={note} />;
};
export default NoteEditModal;
