import React from "react";
import { Badge } from "@chakra-ui/react";
const BadgeItem = ({ title }) => {
  return (
    <Badge
      padding={1}
      paddingLeft={2}
      paddingRight={2}
      borderRadius="md"
      colorScheme={"purple"}
      color="black"
    >
      {title}
    </Badge>
  );
};

export default BadgeItem;
