import { Badge, Box, Button, Container, Text } from "@chakra-ui/react";
import { Menu, MenuButton, MenuList, MenuItem } from "@chakra-ui/react";
import React from "react";
import { HamburgerIcon } from "@chakra-ui/icons";
import { ImExit } from "react-icons/im";
import { useNavigate } from "react-router-dom";
import { UserState } from "../Context/UserProvider";
import ProfileModal from "./ProfileModal";

const NotePageHeader = () => {
  const { user } = UserState();
  const navigate = useNavigate();
  console.log("note header getderived", { user });
  return (
    <Container
      p={3}
      bg={"white"}
      maxW={"100%"}
      borderRadius={"lg"}
      display={"flex"}
      justifyContent={"space-between"}
      alignItems={"center"}
    >
      <Text>
        <Badge
          padding={1}
          paddingLeft={2}
          paddingRight={2}
          borderRadius="md"
          colorScheme={"yellow"}
          color="black"
          fontSize={"25px"}
        >
          Your Data 🔥
        </Badge>
      </Text>
      <Menu>
        <MenuButton as={Button} rightIcon={<HamburgerIcon />}></MenuButton>
        <MenuList>
          <ProfileModal user={user.userInfo} title={"Profile"} />
          <MenuItem
            onClick={(e) => {
              localStorage.removeItem("userInfo");
              navigate("/");
              return;
            }}
          >
            <Box
              display={"flex"}
              justifyContent={"space-between"}
              width={"100%"}
              alignItems={"center"}
            >
              <Text>Logout</Text>
              <ImExit />
            </Box>
          </MenuItem>
        </MenuList>
      </Menu>
    </Container>
  );
};

export default NotePageHeader;
